@import url('https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Ubuntu:wght@300;400;500;700&display=swap');
*
*:before,
*:after{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
  
body{
  width: 100%;
  height: 100vh;
  font-family:'Abril Fatface',
  cursive;
  background: #000;
  color: #fff;
  overflow-x: hidden
}
.App{
  display: flex;
  flex-direction: column;
  align-items: center;
}

img {
  width: 400px;
  height: 600px;
}

.logo {
  padding-top: 50px;
  width: 220px;
  height: 200px;
}

.navbar {
  margin-block: 70px;
  color: #fff;
}

.navbar p{
  font-size: 22px;
}

a{
  text-decoration: none;
  color: #fff;
  font-size: 26px;
  padding-inline: 10px;
  transition: all 0.3s ease;
}

a.active {
  color: #DEBD30;
}

a:hover{
  color: #DEBD30;
}

p{
  color: #DEBD30;
  font-size: 32px;
  margin-block-end: -5px;
}

.container{
  display: flex;
}

.container span:first-child{
  padding-right: 100px;
}

